body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  height: 100%;
  width: 100%;
  background: url(/static/media/bussiness-computer-dim.933297b7.jpg) no-repeat fixed center center;
}
.app {
  height: 100%;
  width: 100%;
}
.contentContainer {
  position: relative;
  top: 10vh;
  margin: 0 auto 10px;
  height: auto;
  width: 70vw;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 20px;
  border: 3px solid white;
  color: white;
  text-align: center;
}
h1 {
  font-size: 50px;
}
h2 {
  font-size: 30px;
}
p {
  font-size: 20px;
  text-decoration: none;
}

@media (max-width: 920px) {
  .contentContainer {
    width: 90vw;
  }
  h1 {
    font-size: 40px;
  }
  h2 {
    font-size: 25px;
  }
  p {
    font-size: 15px;
  }
}

.logo {
	height: 75px;
	width: 75px;
	margin: 20px 30px 0 30px;
	border: 3px solid white;
	border-radius: 20px;
	padding: 10px;
}
.desktopNav {
	display: flex;
	flex-direction: row;
	height: 30px;
	justify-content: space-between;
}
.desktopNav ul{
	margin-top: 35px;
	margin-right: 35px;
	height: 30px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-between;
}
.desktopNav ul li {
	cursor: pointer;
	margin: 0 auto;
	list-style-type: none;
	padding: 0 20px 0 20px;
	margin: 0 30px 0 30px;
	color: white;
	border: 3px solid white;
	text-align: center;
	border-radius: 20px;
}
.phoneNav {
	display: none;
}

li:hover {
	transition: ease-in 0.2s;
	background: rgba(255, 255, 255, 0.2);
}
@media (max-width: 950px) {
	.desktopNav {
		display: none;
	}
	.phoneAboutMe {
		border-left: 3px solid white;
	}
	.phoneNav {
		text-align: center;
		position: relative;
		height: 76px;
		margin: 0;
		width: 100%;
		display: block;
		background: rgba(255, 255, 255, 0.1);
		border-bottom: 3px solid white;
	}
	.phoneNav ul {
		position: relative;
		padding: 0;
		margin: 0;
		height: 76px;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		flex-wrap: wrap;
	}
	.phoneNav ul li {
		cursor: pointer;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0;
		flex: 1 1;
		height: 76px;
		list-style-type: none;
		color: white;
		border-right: 3px solid white;
	}

}
.aboutMe {
	text-align: center;
	color: white;
}
.paragraphContainer {
	margin: 0 40px 0 40px;
	border-top: 2px solid white;
	font-size: 20px;
}
.technologiesContainer {
	text-align: center;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	border-top: 2px solid white;
}

.iconContainer img {
	height: 100px;
	padding: 20px;
}

@media (max-width: 480px) {
	.iconContainer img {
		height: 75px;
		padding: 10px;
	}
}
.projectsContainer {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	margin: 40px;
}
.projects {
	position: relative;
	height: 300px;
	width: 300px;
	border-radius: 20px;
	border: 3px solid white;
	overflow: hidden;
	margin: 20px;
}
.phoneSummary {
	display: none;
}
.robofriends {
	background-image: url(/static/media/Robofriends.bacfb38d.png);
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	height: 300px;
	width: 300px;
	border-radius: 20px;
}
.smartBrain {
	background-image: url(/static/media/Smartbrain.b5ec3d54.png);
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	height: 300px;
	width: 300px;
	border-radius: 20px;
}
.resumeSite {
	background-image: url("https://i.imgur.com/QQQ1fHz.png");
	background-size: cover;
	background-position: center center;
	background-repeat: no-repeat;
	height: 300px;
	width: 300px;
	border-radius: 20px;
}

.shadowBox {
	z-index: 2;
	background: rgba(0,0,0,0);
	color: rgba(0,0,0,0);
	position: absolute;
	top: 0;
	left: 0;
	height: 300px;
	width: 300px;
	border-radius: 20px;

}
.shadowBox p {
	margin: 0 10px;
	position: relative;
	top: 50%;
	-webkit-transform: perspective(1px) translateY(-50%);
	        transform: perspective(1px) translateY(-50%);
}
.shadowBox:hover {
	z-index: 2;
	transition: 0.3s ease-out;
	background: rgba(0, 0, 0,0.9);
	display: inline-block;
	vertical-align: middle;
	color: white;
	height: 300px;
	width: 300px;
}

@media (max-width: 920px) {
	.projectsContainer {
		flex-direction: column;
	}
	.projects {
		height: 200px;
		width: 200px;		
	}
	.phoneSummaryContainer {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;
	}
	.phoneSummary {
		display: inline-block;
		float: right;
		color: white;
		align-self: ;
	}
	.shadowBox {
		display: none;
	}
	.robofriends {
		height: 200px;
		width: 200px;
	}
	.smartBrain {
		height: 200px;
		width: 200px;
	}
	.resumeSite {
		height: 200px;
		width: 200px;
	}
}
@media  (max-width: 480px) {
	.projectsContainer {
		margin: 0;
	}
	.projectsContainer p {
		font-size: 14px;
	}
	.projects {
		margin: 10px;
		height: 150px;
		width: 150px;
	}
	.robofriends {
		height: 150px;
		width: 150px;
	}
	.smartBrain {
		height: 150px;
		width: 150px;
	}
	.resumeSite {
		height: 150px;
		width: 150px;
	}
}

.contactMe p {
	margin-left: 10px;
}
.contactInfo {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
	align-items: center;
}
